<template>
  <v-card class="elevation-1 pa-3 login-card">
    <v-card-text>
      <div class="layout column align-center">
        <img src="/static/logov.svg" alt="Vantum" height="160" />
        <h1 class="flex my-4 primary--text">Admin Dashboard</h1>
      </div>
      <v-form>
        <v-text-field append-icon="person" name="login" label="Login" type="text" v-model="email"></v-text-field>
        <v-text-field
          append-icon="lock"
          name="password"
          label="Password"
          id="password"
          type="password"
          v-model="password"
        ></v-text-field>
      </v-form>
    </v-card-text>
    <div class="login-btn">
      <!-- <v-btn icon>
        <v-icon color="blue">fa fa-facebook-square fa-lg</v-icon>
      </v-btn>
      <v-btn icon>
        <v-icon color="red">fa fa-google fa-lg</v-icon>
      </v-btn>
      <v-btn icon>
        <v-icon color="light-blue">fa fa-twitter fa-lg</v-icon>
      </v-btn> -->
      <v-spacer></v-spacer>
      <v-btn block color="primary" @click="login" :loading="loading">Login</v-btn>
    </div>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    loading: false,
    email: "",
    password: ""
  }),

  methods: {
    login: function() {
      this.loading = true
      let email = this.email
      let password = this.password
      this.$store
        .dispatch("login", { email, password })
        .then(() => {
          this.loading = false
          this.$router.push("/dashboard")
        })
        .catch(err => {
          if (err.message == "notallowed") {
            this.$router.push("/403")
          }
          if (err.response.data.message == "Invalid Password") {
            this.$toast("Senha inválida.", {
              color: "red",
              icon: "warning",
              dismissable: true,
              queueable: true
            })
          } else if (err.response.data.message == "User not found") {
            this.$toast("Usuário não cadastrado.", {
              color: "red",
              icon: "warning",
              dismissable: true,
              queueable: true
            })
          } else if (err.response.data.message == "Email not confirmed") {
            this.$toast("Email não confirmado.", {
              color: "yellow",
              icon: "warning",
              dismissable: true,
              queueable: true
            })
            this.loading = false
          }
        })
    }
  }
}
</script>
<style scoped lang="css"></style>
